@tailwind base;
@tailwind components;
@tailwind utilities;

@layer components {
    .nav{
        @apply py-4 fixed top-0 left-0 right-0 bg-transparent z-10 transition-all ease-linear duration-75;
    }
    .nav.scroll{
        @apply top-[-500px] duration-[700ms];
    }
    .nav .nav-wrapper{
        @apply flex justify-between items-center max-w-6xl lg:max-w-7xl mx-auto font-gotham-black text-white tracking-wide;
    }

    .nav .item-wrapper{
        @apply flex flex-col items-center justify-center text-lg font-semibold;
    }

    .btn{
        @apply p-2 text-lg font-semibold font-gotham-black;
    }

    .banner-section, .about-section, .variant-section, .article-section, .explore-section, .contact-section{
        @apply min-h-screen w-full bg-cover bg-left-top bg-no-repeat;
    }

    .about-section, .variant-section, .article-section, .explore-section, .contact-section{
        @apply pt-4 relative;
    }

    .banner-content{
        @apply h-full max-w-4xl md:max-w-5xl mx-auto;
    }

    .banner-content .splide__pagination__page{
        @apply w-4 h-4 bg-milklife-blue-darken;
    }

    .banner-content .splide__pagination__page.is-active{
        @apply w-4 h-4 bg-white scale-100;
    }

    .variant-item{
        @apply flex flex-col items-center justify-between text-lg font-semibold text-white py-3 px-4 border-8 border-white rounded-md mx-auto max-w-[200px] h-[528px];
    }

    .variant-title{
        @apply text-center font-gotham-black text-base;
    }

    .page-nav-item{
        @apply w-5 h-5 rounded-full bg-milklife-blue transition-all ease-in-out duration-700;
    }
    .page-nav-item.active{
        @apply bg-milklife-blue-darken;
    }

    .page-title{
        @apply font-koara-bold text-5xl text-center text-white tracking-wider;
    }

    .about-section.page .about-content, .article-section.page .article-content, .explore-section.page .explore-content, .contact-section.page .contact-content{
        @apply max-w-5xl md:max-w-6xl mx-auto;
    }

    .about-title, .article-title{
        @apply text-center font-gotham-bold text-3xl text-milklife-orange;
    }

    .about-body .detail .title{
        @apply font-koara-bold text-3xl text-milklife-orange mb-2;
    }

    .nav-artikel-wrapper{
        @apply flex space-x-10 justify-center items-center mx-auto max-w-max;
    }

    .nav-artikel-wrapper .nav-artikel{
        @apply relative p-10 block text-white cursor-pointer;
    }
    .nav-artikel-wrapper .nav-artikel.active, .nav-artikel-wrapper .nav-artikel:hover{
        @apply text-milklife-blue-darken;
    }
    .nav-artikel-wrapper .nav-artikel span{
        @apply block;
    }
}
